.item{
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    scroll-snap-align: start;
}

.item_container{
    width: 40vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.item_text{
    margin-top: 20%;
    display: flex;
    flex-direction: column;
    font-size: 30px;
    color: #393c41;
    align-items: center;
    flex: 1;
}

.item_text_desc{
    font-weight:200 ;
    font-size: 14px;
}

.item_lowerThird{
    margin-bottom: 5%;
    display: flex;
    flex-direction: column;
}

.items_button{
    display: flex;
}

.items_expand{
    color: black;
    width: 100%;
    margin-top: 3%;
    margin-left: 50%;
    justify-content: center;
  

}